.profile_corporate{
    background-color: #0a382b;
    width: 100vw;
    height: 100vh;
}
.profile_corporate section .form-container h3{
    color: white;
    font-size: 25px;
    letter-spacing: 1px;
    border-bottom: 3px solid white;
    margin:0px auto 20px auto;
    width: 300px;
}
.profile_corporate section header{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #2AF6BC;
    padding: 10px 15px;
    width: 100%;
    text-align: center;
    border-radius: 15px;
    position: relative;
    border: 1px solid #707070;
    opacity:0.68;
}

.profile_corporate section{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 600px;
    position: absolute;
    right: 200px;
    top:50px;
}

.profile_corporate section header .icon{
    position: absolute;
    left: 20px;
    bottom: 5px;
}
.profile_corporate section header .text{
    font-size: 20px;
    letter-spacing: 1px;
    font-weight: 800;
}
.profile_corporate .back {
    position: absolute;
    right: 20px;
    top: 20px;
}
.profile_corporate .back img{
    width: 30px;
    height: 30px;
}
.profile_corporate .back span{
    border: 2px solid white;
    padding: 5px 55px 5px 5px;
    position: relative;
    right: -50px;
    border-radius: 20px 0% 0% 20px;
    color: white;
    opacity: 0;
    visibility: hidden;
    transition: all 0.6s ease;
    border-right: none;
}
.back:hover.back span{
    opacity: 1; 
    visibility: visible;
}

.profile_corporate .section2 img{
    position: absolute;
    left:0;
    top:0px;
    width: 27%;
    height: 100%;
}

.profile_corporate section .form-container{
    background-color: #22594e;
    width: 100%;
    text-align: center;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border-radius: 50px;
    border: 1px solid #707070;
    height: 480px;
}
.profile_corporate section .body .information{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 3px solid #c4f9ae;
    width: 80%;
    margin: 15px auto;
    border-radius: 15px;
    padding: 0px 10px;
}
.profile_corporate section .body .information label{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.profile_corporate section .body .information input{
    width: 100%;
    height: 100%;
    padding: 10px;
    background:none;
    border: none;
    color:#fff;
    font-size: 20px;
}
.profile_corporate .section-ar .body .information input{
    text-align: right;
    font-family: 'Readex Pro', sans-serif;
}

.profile_corporate section .body .information input:focus{
    outline:none ;
}
.profile_corporate section .body .information img{
    width: 25px;
}
.profile_corporate section .body .information p{
    font-size: 20px;
    text-align: left;
    color: white;
    position: relative; 
    top:6px 
}

.profile_corporate section .footer .next{
    text-align: right;
    background-color: #2AF6BC;
    border: none;
    border-radius: 30px;
    font-size: 18px;
    position: relative;
    border: 2px solid #fff;
    transition: all 0.5s ease;
    opacity: 0.68;
}